.btn-load-more.is-hidden {
    display: none;
  }
  
  .btn-load-more {
    display: block;
    margin: 25px auto 0 auto;
    padding: 8px 20px;
    border: none;
    border-radius: 5px;
    background-color: #3f57b3;
    box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 2px 6px 2px rgb(60 64 67 / 15%);
    font: inherit;
    color: white;
    cursor: pointer;
  }
  
  .btn-to-top {
    position: fixed;
    bottom: 3vh;
    right: 1vw;
    z-index: 1000;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: #d0d3dd;
    box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 2px 6px 2px rgb(60 64 67 / 15%);
    color: white;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    backdrop-filter: blur(2px);
  }
  
  .btn-to-top--visible {
    opacity: 0.7;
    visibility: visible;
  }
  
  .btn-load-more:hover,
  .btn-to-top:hover {
    background-color: #8e92a1;
    transition: all 0.5s;
  }